import { Column } from "@ant-design/plots";
import React from "react";
import { Card, Col, Row, Typography } from "antd";

const { Title } = Typography;
const CBarChartBranch = ({ data = [] }) => {
  const config = {
    data: data,
    xField: "name",
    yField: "total",
    seriesField: "name",
    label: {
      position: "middle",
      content: () => {
        return "";
      },
      layout: [
        {
          type: "interval-adjust-position",
        },
        {
          type: "interval-hide-overlap",
        },
        {
          type: "adjust-color",
        },
      ],
    },
  };

  return (
    <Col span={16}>
      <Card>
        <Row style={{ justifyContent: "center" }}>
          <Col span={24} style={{ textAlign: "center" }}>
            <Title level={4}>Total Asset By Branch</Title>
            <Column {...config} />
          </Col>
        </Row>
      </Card>
    </Col>
  );
};

export default CBarChartBranch;
